















import App from '../../../../App.vue'
import Api from '../../../../api.js'
export default {
	data() {
		return {
			scan: 'true',
			scrollTop: 0,
			shops: [],
			activity_id: '',
			activities: [],
			activitystate: []
		}
	},
	onShow() {
		var that = this;
		if (App.getToken()) {
						uni.request({
					url: Api.getActivity(),
					header: {
						token: App.getToken()
					},
					data: {
						companyId:App.getcompany().company_id
					},
					success: function(res) {
						console.log(res)
						if (res.data.code == 16000) {
							uni.showToast({
								title: '您没有权限',
								duration: 2000,
								icon:'none'
							})
						} else {
							if (res.data.value.length > 0) {
								for (let iterm in res.data.value) {
									that.activities = res.data.value;
								}
							}else{
								uni.showToast({
									title: '您暂时没有活动',
									duration: 2000,
									icon:'none'
								})
								}
						}
				
						console.log(that.activities);
					}
				})
	
		} else {
			uni.showToast({
				title: '未登录',
				icon: 'none'
			})
			setTimeout(function() {
				uni.navigateTo({
					url: '../login'
				})
			}, 1000)
		}
	},
	methods: {
		intocoupons: function(index) {
			var that = this;
			that.activity_id = that.activities[index].activity_id;
			uni.navigateTo({
				url: 'intoCoupons/intoCoupons?activity_id=' + JSON.stringify(that.activity_id) + '&shops=' + JSON.stringify(
					that.shops)
			});
		}
	}
}
